export default {
  props: {
    name: {type: String}
  },
  data () {
    return {
      author: {}
    }
  },
  mounted() {
    switch(this.name.toLowerCase()) {
      case 'ashok':
        this.author = {
          name: 'Ashok Gelal',
          avatar: 'https://pbs.twimg.com/profile_images/1327311742829883392/Gx6mG7kH_400x400.jpg',
          handle: 'ashokgelal',
        }
        break;
        default:
         break;
    }
  }

}
